*,
*::after,
*::before{
    box-sizing: border-box;
    transform-style: preserve-3d;
}


.body{
    display: grid;
    place-items: center;
    min-height: 100vh;
}

:root{
    --size:120;
    --coefficient:1px;
    --timeline:2.6s;
    --delay:0.65s;
    --rotation-y:-24;
    --rotation-x:28;
    --color1:#001;
    --color2:#002;
    --color3:#003;
}

.scene{
    position: relative;
    transform: translate3d(0,0,100vmin) rotateX(calc(var(--rotation-y,0)*1deg)) rotateY(calc(var(--rotation-x,0)*1deg)) rotateX(0deg);
}

.body{
    transform-origin: 50% 50%;
    animation: scale var(--timeline) var(--delay) infinite linear;
}

@keyframes scale {
    0%,10%{
        transform: scaleX(1) scaleY(1);
    }
    35%,100%{
        transform: scaleX(0.5) scaleY(0.5);
    }
}

.shadow{
    width: calc(var(--size) * var(--coefficient));
    position: absolute;
    bottom: 0;
    aspect-ratio: 1;
    transform-origin: 50% 50%;
    transform: rotateX(90deg) scale(0.96);
    animation: squish-squosh  var(--timeline) var(--delay) infinite,fade  var(--timeline) var(--delay) infinite;
    background: #555;
}

.loader{
    --depth:var(--size);
    --color:var(--color1,#8338EC);
    width: calc(var(--depth) * var(--coefficient));
    aspect-ratio: 1;
    transform-origin: 50% 50%;
    animation: squish-squosh  var(--timeline) var(--delay) infinite;
}

.spinner{
    animation: spin  var(--timeline) var(--delay) infinite;
}

.jumper{
    animation: jump  var(--timeline) var(--delay) infinite  ;
}

@keyframes squish-squosh {
    0%,50%,60%{
        scale: 1 1 1;
    }
    10%,35%{
        scale: 1.2 0.8 1.2;
    }
    25%{
        scale: 0.8 1.2 0.8;
    }
    70%{
        scale: 1 1 2;
    }
    80%{
        scale: 2 1 2;
    }
    90%,100%{
        scale: 2 2 2;
    }
}

@keyframes fade {
    0%,10%,40%,50%,60%,100%{
        opacity: 1;
    }
    25%{
        opacity: 0.5;
    }
}

@keyframes spin {
    0%,10%{
        rotate: 0deg;
    }
    30%,100%{
        rotate: -360deg;
    }
}

@keyframes jump {
    0%,10%,35%,50%{
        translate: 0 0;
    }
    25%{
        translate: 0 -150%;
    }
} 

.cuboid{
    width: 100%;
    height: 100%;
    position: relative;
}

.cuboid_side{
    background: var(--color);
    position: absolute;
    box-shadow: 0 0 10px #cb78d8;
}

.cuboid_side:nth-of-type(1){
    height:  120px;
    width:100%;
    top: 0;
    transform: translate(0,-50%) rotateX(90deg);
}

.cuboid_side:nth-of-type(2){
    --color:var(--color3,#FF006E);
    height: 100%;
    width:120px;
    top: 50%;
    transform: translate(50%,-50%) rotateY(90deg);
}

.cuboid_side:nth-of-type(3){
    height:120px;
    width: 100%;
    transform: translate(0,50%) rotateX(90deg);
}

.cuboid_side:nth-of-type(4){
    --color:var(--color3,#FF006E);
    height: 100%;
    width:120px;
    top: 50%;
    transform: translate(-50%,-50%) rotateY(90deg);
}

.cuboid_side:nth-of-type(5){
    --color:var(--color2,#FF006E);
    height: 100%;
    width: 100%;
    transform: translate3d(0, 0, calc(var(--depth,20)*(var(--coefficient)*0.5)));
    top: 0;
    left: 0;
}

.cuboid_side:nth-of-type(6){
    height: 100%;
    width: 100%;
    transform: translate3d(0, 0, calc(var(--depth,20)*(var(--coefficient)*-0.5))) rotateY(180deg);
    top: 0;
    left: 0;
}

